<template>
  <div class="project-page">
    <div class="project-page__back-button">
      <router-link :to="{ path: '/work' }"> <b-icon-arrow-bar-left> </b-icon-arrow-bar-left> Back to portfolio
      </router-link>
    </div>

    <div class="project-page__title">
      <h1 class="mb-5">Synthetic AIS Data Generation for Maritime Route Modeling</h1>
    </div>

    <div class="row mb-3">
      <div class="col-12 col-md-auto mb-2">
        <b-button href="SyntheticDataReport.pdf" target="_blank" variant="primary">
          View Report
        </b-button>
      </div>
      <div class="col-12 col-md-auto mb-2">
        <b-button href="https://github.com/nina-verbeeke-tue/mostlyai-synthetic-vessel-trajectories"
          target="_blank" variant="primary">
          View Code
        </b-button>
      </div>
    </div>

    <div class="project-page__text-block">

      <h4> Project Context </h4>
      <p>
        Maritime security agencies increasingly rely on data-driven tools to understand vessel behaviour and detect
        illegal activities at sea. The MARIT-D (Multi AI Real-time Intelligence Tool on Drugs) project is a
        collaborative pan-EU initiative aimed at disrupting illicit trafficking in Europe by leveraging data and
        intelligence. Within the MARIT-D, Automatic Identification System (AIS) data plays a key role. AIS continuously
        reports vessel positions, providing valuable insights into ship movements.
      </p>
      <p>
        However, AIS data rarely includes labelled examples of suspicious behaviour, and unusual patterns are scarce.
        This creates a major challenge: without high-quality training data, it becomes difficult to evaluate or improve
        models that detect risky vessel interactions. To address this problem, the project explores the use of synthetic
        AIS trajectories: realistic vessel movements that replicate real-world behaviour.
      </p>

      <h4> Personal attributes extraction </h4>
      <p>
        The focus of this project is the creation of synthetic vessel trajectories using a generative, autoregressive
        sequence model. Instead of manually defining movement rules, the model learns directly from real AIS data. Each
        vessel trip is represented as a sequence of geospatial positions over time. By modelling how real vessels move,
        the system can generate new, realistic routes that follow the same patterns.
      </p>
      <p>
        This synthetic data helps analysts test detection tools under diverse scenarios and provides a scalable way to
        enrich training datasets, especially when real-world examples of suspicious behaviour are too rare to capture
        reliably.
      </p>

      <h4>Approach</h4>
      <p>
        The solution builds on the TabularARGN framework, a modern autoregressive architecture designed for generating
        synthetic tabular and sequential data. Unlike rule-based simulators, this approach leverages deep learning to
        model complex spatial and temporal patterns directly from raw AIS trajectories.
      </p>

      <p>
        A two-table setup is used:
      <ul>
        <li> Trips table (static context): each vessel trip, identified by a unique trip ID </li>
        <li> Positions table (sequential data): ordered geospatial points representing the vessel's path </li>
      </ul>
      </p>

      <p> The model learns the relationship between these tables, enabling it to generate sequences of
        latitude–longitude coordinates while conditioning on trip-level context. Geospatial coordinates are encoded
        using specialized quadtile embeddings, allowing the model to capture fine-grained spatial structure.
      </p>
      <p> After training, the generator produces new trajectories by sampling from the learned probability distribution,
        creating realistic but non-identifiable vessel movements.
      </p>

      <h4> Pipeline </h4>
      <p>
        An end-to-end pipeline was developed to process real AIS data and produce synthetic trajectories:
      <ul>
        <li> Data Pre-processing
          <ul>
            <li> Downsampling of trajectories for stable learning </li>
            <li> Cleaning and standardization </li>
            <li>Geospatial encoding of coordinates </li>
          </ul>
        </li>
        <li> Model training
          <ul>
            <li> Training the TabularARGN model on 3850 real trajectories </li>
            <li> Leveraging a conditional sequential architecture for route simulation </li>
          </ul>
        </li>
        <li> Synthetic Generation
          <ul>
            <li> Producing new vessel trajectories
            </li>
            <li> Visualizing and validating generated routes</li>
          </ul>
        <li> Exporting & Reusability
          <ul>
            <li> Saving the generator for reuse </li>
            <li> Exporting synthetic data to standard formats </li>
          </ul>
        </li>
        </li>
      </ul>

      The pipeline is designed to be reproducible and scalable.
      </p>

      <h4> Outcome </h4>
      <p>
        The trained model successfully generates realistic vessel trajectories that reproduce the patterns observed in
        Danish waters. These synthetic routes can support maritime analysts by:
      <ul>
        <li> enriching training datasets for behaviour-detection algorithms </li>
        <li> enabling safe model development without exposing sensitive operational data </li>
        <li> facilitating scenario exploration for rare or emerging vessel behaviours </li>
      </ul>
      </p>
    </div>

    <div class="project-page__back-button-bottom">
      <b-container>
        <b-row class="justify-content-md-center">
          <b-col cols="12">
            <router-link :to="{ path: '/work' }"> <b-button variant="light">See more work</b-button> </router-link>
          </b-col>
        </b-row>
      </b-container>
    </div>
  </div>
</template>

